import { UmbBlockCatalogueModalData, UmbBlockCatalogueModalElement } from "@umbraco-cms/backoffice/block";
import { customElement } from "@umbraco-cms/backoffice/external/lit";
import { UMB_MODAL_CONTEXT } from "@umbraco-cms/backoffice/modal";


@customElement('custom-umb-block-catalogue-modal')
export class CustomUmbBlockCatalogueModalElement extends UmbBlockCatalogueModalElement {
  

  constructor() {
    super();

    this.consumeContext(UMB_MODAL_CONTEXT, (modalContext) => {
      const asJson = JSON.stringify(modalContext?.data);
      if(asJson) {
        const tempData = JSON.parse(asJson);
        console.log(tempData)
        // we can manipulate the data here
        this.data = (tempData as UmbBlockCatalogueModalData) ?? {};
      }
		});
    
  }
}

export default CustomUmbBlockCatalogueModalElement;

declare global {
	interface HTMLElementTagNameMap {
		'custom-umb-block-catalogue-modal': CustomUmbBlockCatalogueModalElement;
	}
}
